import axios from "axios";

const ADD_ARRAY_TO_SLIDES = 'ADD_ARRAY_TO_SLIDES';
const INIT_SLIDER = 'INIT_SLIDER';
const ADD_SELL_LEADERS = 'ADD_SELL_LEADERS';
const INIT_SELL_LEADERS = 'INIT_SELL_LEADERS';
const CLEAN_SELL_LEADERS = 'CLEAN_SELL_LEADERS';

export const addLeaders = (objItem) => {
    return {
        type: ADD_SELL_LEADERS,
        objItem
    }
}

export const addArrayToSlides = (array) => {
    return {
        type: ADD_ARRAY_TO_SLIDES,
        array
    }
}
export const initSliderAC = (init) => {
    return {
        type: INIT_SLIDER,
        init
    }
}
export const initSellLeadersAC = (init) => {
    return {
        type: INIT_SELL_LEADERS,
        init
    }
}
export const cleanSL = () => {
    return {
        type: CLEAN_SELL_LEADERS,
    }
}

let initialState = {
 slidesResult: [],
 initSlider: false,
 initSellLeaders: false,
 slides: [
     ["3407839", "2014851", "2368166", "2202103", "2364521"],
     ["2006123", "1913494", "2361998", "2330523", "2215878"],
     ["2200596", "2315906", "2241285", "2344872", "2305801"]

 ],
    listCategories: [
        {minecraft: "124863"},
        {steam: "124864"},
        {origin: "124865"},
        {uplay: "124866"},
        {xbox: "124867"},
        {software: "124868"}
    ],
    sellLeaders: []

}

const ProductsReducer = (state = initialState, action) => {

    switch (action.type) {

        case ADD_SELL_LEADERS: return {
            ...state,
            sellLeaders: action.objItem
        }
        case ADD_ARRAY_TO_SLIDES: return {
            ...state,
            slidesResult: [...state.slidesResult, action.array]
        }
        case INIT_SLIDER: return {
            ...state,
            initSlider: action.init
        }
        case INIT_SELL_LEADERS: return {
            ...state,
            initSellLeaders: action.init
        }
        case CLEAN_SELL_LEADERS: return {
            ...state,
            sellLeaders: []
        }

        default: return state;
    }
}


export const requestItemsFromCategories = (category) => async (dispatch) => {
    let categories = initialState.listCategories;
    let setArr = []

    if(!initialState.initSellLeaders) {
        for (let i = 0; i < categories.length; i++) {
          await axios.get(`https://api.digiseller.ru/api/shop/products?seller_id=549328&category_id=${Object.values(categories[i])}`).then(async res => {
              let items = res.data.product


              let setDataState = (sortArray) => {
                  let resultArray = [];
                  console.log("sort arr " + sortArray.length)
                  for (let s = 0; s < sortArray.length; s++) {
                      if (s === 60) break
                      resultArray.push({
                          id: sortArray[s].id,
                          cnt_sell: sortArray[s].cnt_sell === -1 ? "Скрыто" : sortArray[s].cnt_sell,
                          price: sortArray[s].price_rub,
                          imgItem: `https://graph.digiseller.ru/img.ashx?id_d=${sortArray[s].id}&w=210&h=145`
                      })
                  }
                  dispatch(addLeaders(resultArray));
                  dispatch(initSellLeadersAC(true))
              }
              for (let r = 0; r < items.length; r++) {
                  setArr.push(items[r].id)
                  if ((i + 1) === categories.length && (r == items.length - 1)) {
                      await axios.get(`https://api.digiseller.ru/api/products/list?ids=${setArr}&lang=ru-RU`).then(resIds => {
                          if (category === "lead") {
                              setDataState(resIds.data.sort((a, b) => b.cnt_sell - a.cnt_sell))
                          } else if (category === "rec") {
                              setDataState(resIds.data.sort(() => Math.random() - 0.5))
                          } else if (category === "dis") {
                              setDataState(resIds.data.sort((a, b) => a.price_rub - b.price_rub))
                          }
                      })
                  }
              }
          })
        }
    }
}

export const requestItems = () => async (dispatch) => {
    let slides = initialState.slides;
    let tempString = '';
    try {
        for (let i = 0; i < slides.length; i++) {
            for (let p = 0; p < 5; p++) {
                tempString += slides[i][p] + ((slides.length-1 == i && p == 4) ? "" : ",");
            }}

        axios.get(`https://api.digiseller.ru/api/products/list?ids=${tempString}&lang=ru-RU`).then(response => {
            let items = response.data;
            let tempArr = [];
            for (let q = 0; q < items.length; q++) {
                tempArr.push({
                    id: items[q].id,
                    cnt_sell: items[q].cnt_sell,
                    price_eur: items[q].price_eur,
                    price_rub: items[q].price_rub,
                    price_uah: items[q].price_uah,
                    price_usd: items[q].price_usd,
                    imgItem: `https://graph.digiseller.ru/img.ashx?id_d=${items[q].id}&h=470`
                })
                if (tempArr.length === 5) {
                    dispatch(addArrayToSlides(tempArr));
                    tempArr = [];
                }}
        })
        dispatch(initSliderAC(true));

    } catch (e) {
        console.error("Error delete document: ", e);
    }
}

export default ProductsReducer;